// Slightly right aligned grid
.main-yayamigi {
  @media (min-width: @screen-sm-min) {
    .hidari { width: (@container-sm * .60); margin-left: (@container-sm * -.5); }
    .migi { width: (@container-sm * .40); margin-left: ((@container-sm * .10) - @centerline-border-width); }
